import { Component, OnInit } from '@angular/core';
import { CartService } from '../core/cart.service';
import { UtilityService } from '../core/utility.service';

@Component({
  selector: 'app-cart',
  templateUrl: './cart.page.html',
  styleUrls: ['./cart.page.scss'],
})
export class CartPage implements OnInit {
  data;
  total;
  loaded = false;
  constructor(private cart: CartService, private utility: UtilityService) {}

  ngOnInit() {
    this.getData();
  }

  getData() {
    this.utility.present();
    this.cart.getCart().subscribe((res) => {
      this.data = res.map((v) => {
        v.qty = 1;
        v.price = parseInt(v.productId.price);
        return v;
      });
      this.getTotal();
      this.utility.dismiss();
      this.loaded = true;
      console.log(this.data, this.total);
    });
  }

  getTotal() {
    this.total = this.data.reduce(
      (sum, { price, qty }) => sum + price * qty,
      0
    );
  }

  remove(id) {
    this.utility.confirmationAlert((cb) => {
      if (cb) {
        this.cart.deleteCart(id).subscribe((res) => {
          console.log(res);
          this.getData();
        });
      }
    });
  }

  incrementQty(index) {
    console.log(index);
    if (this.data[index].qty < 10) {
      this.data[index].qty += 1;
      console.log(this.data[index].qty + 1);
      this.getTotal();
    }
  }

  //decrements item

  decrementQty(index: number) {
    console.log(index);

    /*. if item passed then item.qty. */
    if (this.data[index].qty - 1 < 1) {
      this.data[index].qty = 1;
      console.log('item_1-> ' + this.data[index].qty);
    } else {
      this.data[index].qty -= 1;
      console.log('item_2-> ' + index + '  ' + this.data[index].qty);
    }
    this.getTotal();
  }
  navigate() {
    this.utility.setLocal('total', this.total);
    this.utility.setLocal('cartDetail', JSON.stringify(this.data));

    this.utility.navigate('/payment-methode');
  }
  goBack() {
    this.utility.goBack();
  }
}
